const GridExampleVerticallyDivided = () => {
  const animate = () => {

    anime({
      targets: '.path1',
      fillOpacity: 0,
      easing: 'easeInOutQuad',
      direction: 'alternate',
      duration: 1000,
      loop: 1
    });
    anime({
      targets: '.path1',
      strokeDashoffset: [0, anime.setDashoffset],
      easing: 'easeInOutSine',
      duration: 5000,
      delay: function(el, i) { return i * 250 },
      direction: 'reverse',
      loop: true
    });
    anime({
      targets: '.main-card-1',
      opacity: ['0%', '100%'],
      scale: [0.98, 1],
      duration: 800,
      easing: 'easeInOutQuad'
    });
    anime({
      targets: '.main-card-2',
      opacity: ['0%', '100%'],
      scale: [0.99, 1],
      easing: 'easeInOutQuad',
      duration: 900
    });
    anime({
      targets: '.sec-card',
      opacity: ['0%', '100%'],
      scale: [0.99, 1],
      easing: 'easeInOutQuad',
      duration: 1000
    });
  }
  useEffect(() => {
    animate()
  });

  const handleClick = () => {
  }
  return <Grid divided='vertically' >
    <Grid.Row columns={2}>
      <Grid.Column width={16} >
        {/* <Image className='mainImage' alt='Meetesh Kalpesh Mehta' onClick={handleClick} src='/me.jpg' size='medium' style={{ border: 10, borderStyle: 'double' }} centered circular /> */}
        <div style={{ marginTop: 10, marginBottom: 30, display: 'flex', justifyContent: 'center'}}>
          <Animation/>
        </div>
        {/* <Image onClick={handleClick} src='/me.svg' centered /> */}
        {/* <Divider /> */}
        <Tab menu={{ color: 'teal', borderless: false, vertical: false, secondary: true }}  panes={panes} />
      </Grid.Column>
    </Grid.Row>

  </Grid>
}

export default GridExampleVerticallyDivided
